"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

interface BeforeInstallPromptEvent extends Event {
  readonly platforms: string[]
  readonly userChoice: Promise<{
    outcome: 'accepted' | 'dismissed'
    platform: string
  }>
  prompt(): Promise<void>
}

export default function InstallPrompt() {
  const [deferredPrompt, setDeferredPrompt] = useState<BeforeInstallPromptEvent | null>(null)
  const [isInstallable, setIsInstallable] = useState(false)
  const [isInstalled, setIsInstalled] = useState(false)
  const [showPrompt, setShowPrompt] = useState(false)

  useEffect(() => {
    // Vérifier si l'app est déjà installée
    if (window.matchMedia('(display-mode: standalone)').matches) {
      setIsInstalled(true)
      return
    }

    // Écouter l'événement beforeinstallprompt
    const handleBeforeInstallPrompt = (e: Event) => {
      e.preventDefault()
      setDeferredPrompt(e as BeforeInstallPromptEvent)
      setIsInstallable(true)
      setShowPrompt(true)
    }

    // Écouter l'événement appinstalled
    const handleAppInstalled = () => {
      setIsInstalled(true)
      setShowPrompt(false)
      setDeferredPrompt(null)
    }

    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt)
    window.addEventListener('appinstalled', handleAppInstalled)

    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt)
      window.removeEventListener('appinstalled', handleAppInstalled)
    }
  }, [])

  const handleInstallClick = async () => {
    if (!deferredPrompt) return

    deferredPrompt.prompt()
    const { outcome } = await deferredPrompt.userChoice

    if (outcome === 'accepted') {
      setDeferredPrompt(null)
      setShowPrompt(false)
    }
  }

  const handleDismiss = () => {
    setShowPrompt(false)
  }

  if (isInstalled) {
    return (
      <Card className="mb-6 border-green-200 bg-green-50">
        <CardContent className="pt-6">
          <div className="flex items-center justify-center space-x-2">
            <Badge className="bg-green-600">✓ Installé</Badge>
            <span className="text-green-800 font-medium">
              Pomme Croquer est installé sur votre ordinateur !
            </span>
          </div>
        </CardContent>
      </Card>
    )
  }

  if (!showPrompt || !isInstallable) {
    return null
  }

  return (
    <Card className="mb-6 border-green-200 bg-gradient-to-r from-green-50 to-green-100">
      <CardHeader>
        <CardTitle className="text-green-800 flex items-center space-x-2">
          <span>💻</span>
          <span>Installer Pomme Croquer</span>
        </CardTitle>
        <CardDescription className="text-green-700">
          Installez l'application sur votre ordinateur pour un accès rapide et hors ligne
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="flex flex-col sm:flex-row gap-3">
          <Button 
            onClick={handleInstallClick}
            className="bg-green-600 hover:bg-green-700 flex-1"
          >
            📥 Installer maintenant
          </Button>
          <Button 
            variant="outline" 
            onClick={handleDismiss}
            className="border-green-300 text-green-700 hover:bg-green-50"
          >
            Plus tard
          </Button>
        </div>
        <div className="mt-4 text-xs text-green-600">
          <p>✓ Accès hors ligne</p>
          <p>✓ Lancement rapide depuis le bureau</p>
          <p>✓ Notifications push</p>
          <p>✓ Interface native</p>
        </div>
      </CardContent>
    </Card>
  )
}
