"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

export default function DownloadButton() {
  const [isDownloading, setIsDownloading] = useState(false)

  const handleDownload = async () => {
    setIsDownloading(true)
    
    try {
      // Créer un fichier de raccourci pour Windows
      const shortcutContent = `[InternetShortcut]
URL=https://pomme-croquer.vercel.app
IconFile=https://pomme-croquer.vercel.app/icon-192x192.png
IconIndex=0`

      const blob = new Blob([shortcutContent], { type: 'text/plain' })
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = 'Pomme Croquer.url'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      window.URL.revokeObjectURL(url)
    } catch (error) {
      console.error('Erreur lors du téléchargement:', error)
    } finally {
      setIsDownloading(false)
    }
  }

  const handleDownloadMac = async () => {
    setIsDownloading(true)
    
    try {
      // Créer un fichier webloc pour macOS
      const weblocContent = `<?xml version="1.0" encoding="UTF-8"?>
<!DOCTYPE plist PUBLIC "-//Apple//DTD PLIST 1.0//EN" "http://www.apple.com/DTDs/PropertyList-1.0.dtd">
<plist version="1.0">
<dict>
    <key>URL</key>
    <string>https://pomme-croquer.vercel.app</string>
</dict>
</plist>`

      const blob = new Blob([weblocContent], { type: 'application/xml' })
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = 'Pomme Croquer.webloc'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      window.URL.revokeObjectURL(url)
    } catch (error) {
      console.error('Erreur lors du téléchargement:', error)
    } finally {
      setIsDownloading(false)
    }
  }

  const handleDownloadDesktop = async () => {
    setIsDownloading(true)
    
    try {
      // Créer un fichier .desktop pour Linux
      const desktopContent = `[Desktop Entry]
Version=1.0
Type=Application
Name=Pomme Croquer
Comment=Gestionnaire iOS Professionnel
Exec=xdg-open https://pomme-croquer.vercel.app
Icon=https://pomme-croquer.vercel.app/icon-192x192.png
Terminal=false
Categories=Utility;System;`

      const blob = new Blob([desktopContent], { type: 'text/plain' })
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = 'Pomme Croquer.desktop'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      window.URL.revokeObjectURL(url)
    } catch (error) {
      console.error('Erreur lors du téléchargement:', error)
    } finally {
      setIsDownloading(false)
    }
  }

  const detectOS = () => {
    const userAgent = window.navigator.userAgent
    if (userAgent.includes('Windows')) return 'windows'
    if (userAgent.includes('Mac')) return 'mac'
    if (userAgent.includes('Linux')) return 'linux'
    return 'unknown'
  }

  const os = typeof window !== 'undefined' ? detectOS() : 'unknown'

  return (
    <Card className="mb-6 border-green-200 bg-gradient-to-r from-green-50 to-green-100">
      <CardHeader>
        <CardTitle className="text-green-800 flex items-center space-x-2">
          <span>⬇️</span>
          <span>Télécharger Pomme Croquer</span>
        </CardTitle>
        <CardDescription className="text-green-700">
          Téléchargez un raccourci direct vers Pomme Croquer pour votre bureau
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          <div className="flex flex-col sm:flex-row gap-3">
            {os === 'windows' && (
              <Button 
                onClick={handleDownload}
                disabled={isDownloading}
                className="bg-green-600 hover:bg-green-700 flex-1"
              >
                {isDownloading ? '⏳ Téléchargement...' : '🪟 Télécharger pour Windows'}
              </Button>
            )}
            
            {os === 'mac' && (
              <Button 
                onClick={handleDownloadMac}
                disabled={isDownloading}
                className="bg-green-600 hover:bg-green-700 flex-1"
              >
                {isDownloading ? '⏳ Téléchargement...' : '🍎 Télécharger pour macOS'}
              </Button>
            )}
            
            {os === 'linux' && (
              <Button 
                onClick={handleDownloadDesktop}
                disabled={isDownloading}
                className="bg-green-600 hover:bg-green-700 flex-1"
              >
                {isDownloading ? '⏳ Téléchargement...' : '🐧 Télécharger pour Linux'}
              </Button>
            )}
            
            {os === 'unknown' && (
              <div className="grid grid-cols-1 sm:grid-cols-3 gap-2">
                <Button 
                  onClick={handleDownload}
                  disabled={isDownloading}
                  variant="outline"
                  className="border-green-300 text-green-700 hover:bg-green-50"
                >
                  🪟 Windows
                </Button>
                <Button 
                  onClick={handleDownloadMac}
                  disabled={isDownloading}
                  variant="outline"
                  className="border-green-300 text-green-700 hover:bg-green-50"
                >
                  🍎 macOS
                </Button>
                <Button 
                  onClick={handleDownloadDesktop}
                  disabled={isDownloading}
                  variant="outline"
                  className="border-green-300 text-green-700 hover:bg-green-50"
                >
                  🐧 Linux
                </Button>
              </div>
            )}
          </div>

          <div className="p-3 bg-green-100 rounded-lg">
            <div className="flex items-center space-x-2 mb-2">
              <Badge className="bg-green-600">🌐 Lien direct</Badge>
              <span className="text-sm font-medium text-green-800">
                https://pomme-croquer.vercel.app
              </span>
            </div>
            <p className="text-xs text-green-700">
              Vous pouvez aussi copier ce lien et l'ouvrir directement dans votre navigateur
            </p>
          </div>

          <div className="text-xs text-green-600 space-y-1">
            <p>✓ Raccourci bureau automatique</p>
            <p>✓ Accès direct en un clic</p>
            <p>✓ Compatible tous systèmes</p>
            <p>✓ Mise à jour automatique</p>
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
